.aboutSection {
    padding-top: 4vh;
    height: 96vh;
    margin: 0 10rem;
    box-sizing: border-box;
    min-height: fit-content;
    position: relative;
}

.aboutContainerF {
    display: flex;
    gap: 4rem;
    height: 80%;
}

.textP1 {
    text-align: center;
    opacity: 0.8;
    font-size: 20px;
    font-weight: 600;
}

.detailsContainer p {
    opacity: 0.5;
    font-weight: 600;
    font-size: 20px;
}

.aboutTitle {
    font-size: 3rem;
    text-align: center;
}

.aboutPicCntnr {
    display: flex;
    height: 400px;
    width: 400px;
    margin: auto 0;
    /* background: red; */
}

.aboutPic {
    border-radius: 2rem;
    overflow: hidden;
}

.icon {
    cursor: pointer;
    height: 2.2rem;
}

.aboutDetailsCntnr {
    justify-content: center;
    flex-direction: column;
}

.aboutContainers {
    gap: 2rem;
    margin-bottom: 2rem;
    margin-top: 2rem;
}

.aboutDetailsCntnr,
.aboutContainers {
    display: flex;
}

.detailsContainer {
    padding: 1.2rem;
    flex-grow: 1;
    background: white;
    border-radius: 2rem;
    border: rgb(53, 53, 53) 0.1rem solid;
    border-color: rgb(163, 163, 163);
    text-align: center;
    line-height: 2rem;
}

.textContainer p {
    font-size: 20px;
    font-weight: 600;
    opacity: 0.5;
    line-height: 2remrem;
}
.textContainer{
    border: rgb(53, 53, 53) 0.1rem solid;
    border-color: rgb(163, 163, 163);
    padding: 1.2rem;
    border-radius: 2rem;
    
}