.desktopNav {
   justify-content: space-around;
   align-items: center;
   height: 17vh;
}

.navLinks {
   gap: 3rem;
   list-style: none;
   font-size: 1.6rem;
   opacity: 0.8;
}

a {
   color: grey;
   text-decoration: none;
   text-decoration-color: white;
   font-weight: 600;
   font-size: 1.4rem;
}

a:hover {
   color: black;
   text-decoration: underline;
   text-underline-offset: 1rem;
   text-decoration-color: rgb(27, 27, 27);
}

.myName {
   font-size: 3rem;
   font-weight: 600;
   opacity: 0.9;
   color: rgb(27, 27, 27);
   font-family: Brush Script MT, Brush Script Std, cursive;
}

.myName:hover {
   cursor: default;
}

/* Hamburger-Menu */

.hamburgerFix {
   justify-content: space-around;
   align-items: center;
   height: 17vh;
}

.hamburgerNav {
   display: none;
}

.hamburgerMenu {
   position: relative;
   display: inline-block;
}

.hamburgerIcon {
   display: flex;
   flex-direction: column;
   justify-content: space-between;
   height: 24px;
   width: 30px;
   cursor: pointer;
}

.hamburgerIcon span {
   width: 100%;
   height: 2px;
   background-color: black;
   transition: all 0.3 ease-in-out;
}

.menuLinks {
   position: absolute;
   top: 100%;
   right: 0;
   background-color: white;
   width: fit-content;
   max-height: 0;
   overflow: hidden;
   transition: all 0.3 ease-in-out;
}

.menuLinks li a {
   display: block;
   padding: 10px;
   text-align: center;
   font-size: 1.4rem;
   color: rgb(27, 27, 27);
   text-decoration: none;
   transition: all 0.3 ease-in-out;
   opacity: 0.8;
}

.menuLinks li {
   list-style: none;
}

.menuLinks.open {
   max-height: 300px;
}

.hamburgerIcon.open span:first-child {
   transform: rotate(45deg) translate(10px, 5px);
}

.hamburgerIcon.open span:nth-child(2) {
   opacity: 0;
}

.hamburgerIcon.open span:last-child {
   transform: rotate(-45deg) translate(10px, -5px);
}

.hamburgerIcon span:first-child {
   transform: none;
}

.hamburgerIcon span:first-child {
   opacity: 1;
}

.hamburgerIcon span:first-child {
   transform: none;
}